import React, { Component } from 'react';
import {
    Button,
    Modal,
    ModalHeader,
    ModalBody,
    ModalFooter,
    ButtonDropdown,
    DropdownToggle,
    DropdownMenu,
    DropdownItem,
    Row,
    Col
} from 'reactstrap';
import './Interface.css';

const Web3 = require("web3");
const given_provider = Web3.givenProvider || new Web3.providers.HttpProvider("https://mainnet.infura.io/v3/59281a6948714a5db3b477a4e179a08a");
console.log(given_provider);
const web3 = new Web3(given_provider);
const bigInt = require("big-integer");
const cryptico = require("cryptico");
const file_name_list = require("./file_name_list").default;

export default class Interface extends Component {
    constructor(props) {
        super(props);
        this.contract = null;
        this.state = {
            myRSAKey: null,
            userAccount: null,
            currentNetwork: null,
            passwordForCreateToken: "",
            passwordForChangePublicKey: "",
            transferTokenTo: "",
            itemUrls: [],
            isHolder: 0,
            detectAccountChangeIntervalID: null,
            noAccount: true,
            noToken: true,
            messageLog: "",
            messageStatus: "hidden",
            createTokenMessageLog: "",
            createTokenMessageStatus: "hidden",
            createTokenModal: false,
            deleteTokenModal: false,
            transferTokenMessageLog: "",
            transferTokenMessageStatus: "hidden",
            transferTokenModal: false,
            changePublicKeyMessageLog: "",
            changePublicKeyMessageStatus: "hidden",
            changePublicKeyModal: false,
            dropdownOpen: false
        };
    }

    handleChange = (e) => {
        const state = this.state;
        this.setState({...state, [e.target.name]: e.target.value});
    }

    show = (target_name) => () => {
        const state = this.state;
        this.setState({...state, [target_name + "Modal"]: true, [target_name + "MessageStatus"]: "hidden"});
    }

    hide = (target_name) => () => {
        const state = this.state;
        this.setState({...state, [target_name + "Modal"]: false});
    }

    toggle = () => {
        const state = this.state;
        this.setState({...state, dropdownOpen: !this.state.dropdownOpen});
    }

    selectItem = (e) => {
        const file_name = e.target.name;
        if (file_name) {
            this.requestInfo(file_name);
        }
    }

    setContract = () => {
        const state = this.state;
        let my_rsa_key = null;
        try {
            my_rsa_key = parseRSAKey(JSON.parse(localStorage.getItem("RSAKey")));
        } catch (e) {
            console.log(e);
            this.setState({...state, messageStatus: "error", messageLog: "パスワードはローカルストレージに保存されていませんでした。"});
        }

        let salon_token_contract = null;
        let networkId = null;
        const current_provider = web3.currentProvider;
        if (current_provider && current_provider.publicConfigStore) {
            networkId = current_provider.publicConfigStore._state.networkVersion;
            switch (networkId) {
                case "1":
                    salon_token_contract = require("./salon_token_contract1.js");
                    break;
                case "3":
                    salon_token_contract = require("./salon_token_contract3.js");
                    break;
                case "4":
                    salon_token_contract = require("./salon_token_contract4.js");
                    break;
                default:
                    break;
            }
        }
        if (salon_token_contract) {
            const contract_abi = salon_token_contract.abi;
            const contract_address = salon_token_contract.address;
            this.contract = new web3.eth.Contract(contract_abi, contract_address);
            const interval_id = setInterval(() => {
                this.getAccount().then(this.checkAuthority);
            }, 500);
            this.setState({...state, myRSAKey: my_rsa_key, currentNetwork: networkId, detectAccountChangeIntervalID: interval_id});
        } else {
            setTimeout(() => {
                this.setContract();
            }, 10000);
        }
    }

    getAccount = () => {
        const state = this.state;
        const f = (error, accounts) => {
            if (error) {
                console.error(error);
                this.setState({...state, noAccount: true});
                return;
            }
            if (accounts.length === 0) {
                this.setState({...state, noAccount: true});
                return;
            }
            if (state.userAccount !== accounts[0]) {
                this.setState({...state, userAccount: accounts[0], noAccount: false, messageLog: "", messageStatus: "hidden"});
            }
        };
        return web3.eth.getAccounts(f);
    }

    _balanceOf = (sender) => {
        return this.contract.methods.balanceOf(sender).call({from: sender});
    }

    _createToken = (sender, n_of_public_key, e_of_public_key) => {
        return this.contract.methods
            .createToken(n_of_public_key, e_of_public_key)
            .send({from: sender})
            .on('transactionHash', (hash) => {
                console.log(hash);
            }).on('confirmation', (confirmationNumber, receipt) => {
                console.log(confirmationNumber, receipt);
            });
    }

    createToken = () => {
        const state = this.state;
        const user_account = state.userAccount;
        const password = state.passwordForCreateToken;
        if (user_account == null) {
            return;
        }
        if (!password) {
            this.setState({...state, createTokenMessageStatus: "error", createTokenMessageLog: "パスワードを入力してください。"});
            return;
        }
        const f = (balance) => {
            if (Number(balance) === 0) {
                this.setState({...state, createTokenModal: false, passwordForCreateToken: "", messageStatus: "info", messageLog: "トークンが作成されるのを待機しています。この表示がいつまでも変化しない場合は、 MetaMask を開いてトランザクションを承認しているか確認してください。また、トランザクションが取り込まれるまでに時間がかかっている可能性があります。"});
                const my_rsa_key_json = stringifyRSAKey(keyGen(password));
                const my_rsa_key = parseRSAKey(my_rsa_key_json);
                const n = my_rsa_key.n.toString();
                const e = String(my_rsa_key.e);
                this._createToken(user_account, n, e).on("receipt", (result) => {
                    console.log(result);
                    localStorage.setItem("RSAKey", my_rsa_key_json);
                    this.setState({...state, myRSAKey: my_rsa_key, createTokenModal: false, passwordForCreateToken: "", messageStatus: "success", messageLog: "トークンの作成に成功しました。"});
                }).on("error", (error) => {
                    console.error(error);
                    this.setState({...state, createTokenModal: false, passwordForCreateToken: "", messageStatus: "error", messageLog: "トークンの作成に失敗しました。"});
                });
            } else if (Number(balance) > 0) {
                this.setState({...state, createTokenModal: false, passwordForCreateToken: "", messageStatus: "error", messageLog: "あなたはすでにトークンを持っています。"});
            } else {
                this.setState({...state, createTokenModal: false, passwordForCreateToken: "", messageStatus: "error", messageLog: "残高が不明な値です。"});
            }
        };
        const g = (error) => {
            console.error(error);
            this.setState({...state, createTokenModal: false, passwordForCreateToken: "", messageStatus: "error", messageLog: "残高の取得に失敗しました。"});
        };
        this.hide("createToken")();
        this._balanceOf(user_account).then(f, g);
    }

    _deleteToken = (sender) => {
        return this.contract.methods
            .deleteToken()
            .send({from: sender})
            .on('transactionHash', (hash) => {
                console.log(hash);
            }).on('confirmation', (confirmationNumber, receipt) => {
                console.log(confirmationNumber, receipt);
            });
    }

    deleteToken = () => {
        const state = this.state;
        const user_account = state.userAccount;
        if (user_account == null) {
            return;
        }
        const f = (balance) => {
            if (Number(balance) > 0) {
                this.setState({...state, deleteteTokenModal: false, messageStatus: "info", messageLog: "トークンが削除されるのを待機しています。この表示がいつまでも変化しない場合は、 MetaMask を開いてトランザクションを承認しているか確認してください。また、トランザクションが取り込まれるまでに時間がかかっている可能性があります。"});
                this._deleteToken(user_account).on("receipt", (result) => {
                    console.log(result);
                    this.setState({...state, deleteteTokenModal: false, messageStatus: "success", messageLog: "トークンの削除に成功しました。"});
                }).on("error", (error) => {
                    console.error(error);
                    this.setState({...state, deleteteTokenModal: false, messageStatus: "error", messageLog: "トークンの削除に失敗しました。"});
                });
            } else if (Number(balance) === 0) {
                this.setState({...state, deleteteTokenModal: false, messageStatus: "error", messageLog: "あなたはまだトークンを持っていません。"});
            } else {
                this.setState({...state, deleteteTokenModal: false, messageStatus: "error", messageLog: "残高が不明な値です。"});
            }
        };
        const g = (error) => {
            console.error(error);
            this.setState({...state, deleteteTokenModal: false, messageStatus: "error", messageLog: "残高の取得に失敗しました。"});
        };
        this._balanceOf(user_account).then(f, g);
    }

    _checkAuthority = (sender) => {
        return this.contract.methods.checkAuthority(sender).call({from: sender});
    }

    checkAuthority = () => {
        const state = this.state;
        const user_account = state.userAccount;
        if (user_account == null) {
            return;
        }
        const f = (res) => {
            this.setState({...state, isHolder: res.isHolder, noToken: false});
        }
        const g = (err) => {
            this.setState({...state, noToken: true});
        }
        this._checkAuthority(user_account).then(f, g);
    }

    _ownership = (sender) => {
        return this.contract.methods.ownership(sender).call({from: sender});
    }

    _transfer = (sender, to_address, token_id) => {
        return this.contract.methods
            .transfer(to_address, token_id)
            .send({from: sender})
            .on('transactionHash', (hash) => {
                console.log(hash);
            }).on('confirmation', (confirmationNumber, receipt) => {
                console.log(confirmationNumber, receipt);
            });
    }

    transfer = () => {
        const state = this.state;
        const user_account = state.userAccount;
        const to_address = state.transferTokenTo;
        console.log("to address :", to_address);
        if (to_address === "" || !web3.isAddress(to_address) || to_address === user_account) {
            this.setState({...state, transferTokenMessageStatus: "error", transferTokenMessageLog: "宛先を正確に入力してください。"});
            return;
        }
        this._ownership(user_account).then((token_id) => {
            console.log("token id :", token_id);
            const f = (balance) => {
                if (Number(balance) === 0) {
                    this.setState({...state, transferTokenModal: false, transferTokenTo: "", messageStatus: "info", messageLog: "トークンが送金されるのを待機しています。この表示がいつまでも変化しない場合は、 MetaMask を開いてトランザクションを承認しているか確認してください。また、トランザクションが取り込まれるまでに時間がかかっている可能性があります。"});
                    this._transfer(user_account, to_address, token_id).on("receipt", (result) => {
                        console.log(result);
                        this.setState({...state, transferTokenModal: false, transferTokenTo: "", messageStatus: "success", messageLog: "トークンを " + to_address + " に送金しました。"});
                    }).on("error", (error) => {
                        console.error(error);
                        this.setState({...state, transferTokenModal: false, transferTokenTo: "", messageStatus: "error", messageLog: "トークンの送金に失敗しました。"});
                    });
                } else {
                    this.setState({...state, transferTokenModal: false, transferTokenTo: "", messageStatus: "error", messageLog: "送信先のアドレスはすでにトークンを持っています。"});
                }
            }
            const g = (error) => {
                console.log(error);
                this.setState({...state, transferTokenModal: false, transferTokenTo: "", messageStatus: "error", messageLog: "送信先のアドレスの残高取得に失敗しました。"});
            }
            this._balanceOf(to_address).then(f, g);
        }, (error) => {
            console.error(error);
            this.setState({...state, transferTokenModal: false, transferTokenTo: "", messageStatus: "error", messageLog: "あなたはトークンを持っていません。"});
        });
    }

    requestInfo = (file_name) => {
        const state = this.state;
        const user_account = state.userAccount;
        const my_rsa_key = state.myRSAKey;
        if (!my_rsa_key) {
            this.setState({...state, messageStatus: "error", messageLog: "閲覧権限がありません。", dropdownOpen: false});
            return;
        }
        this.setState({...state, messageStatus: "info", messageLog: "残高を取得しています。", dropdownOpen: false});
        this._balanceOf(user_account).then((balance) => {
            if (Number(balance) > 0) {
                this.setState({...state, messageStatus: "info", messageLog: "暗号化されたデータを解読中です。", dropdownOpen: false});
                this._requestInfo(file_name).then((res) => {
                    if (typeof res !== "string") {
                        console.log(res);
                        this.setState({...state, messageStatus: "error", messageLog: "データが取得できませんでした。", dropdownOpen: false});
                        return;
                    }
                    const new_item_urls = {...state.itemUrls, [res.split("?")[0].split("/").slice(-1)[0]]: res};
                    this.setState({...state, itemUrls: new_item_urls, messageStatus: "success", messageLog: "データが取得できました。", dropdownOpen: false});
                    // window.open(res);
                }, (error) => {
                    this.setState({...state, messageStatus: "error", messageLog: "権限を確認できませんでした。", dropdownOpen: false});
                });
            } else if (Number(balance) === 0) {
                this.setState({...state, messageStatus: "error", messageLog: "あなたはまだトークンを持っていません。", dropdownOpen: false});
            } else {
                this.setState({...state, messageStatus: "error", messageLog: "残高が不明な値です。", dropdownOpen: false});
            }
        });
    }

    _requestInfo = (file_name) => {
        const state = this.state;
        const user_account = state.userAccount;
        const my_rsa_key = state.myRSAKey;
        const sign = signRSA(my_rsa_key);
        const params = new URLSearchParams();
        params.set("owner", user_account);
        params.set("sign", sign);
        params.set("file_name", file_name);
        const url = `https://hz9dwl2145.execute-api.ap-northeast-1.amazonaws.com/test/web3-lambda?${params.toString()}`;
        const req = {
            method: "GET",
            mode: "cors",
            cache: "no-cache",
            // dataType: "json"
        };
        return fetch(url, req).then((res) => {
            return res.json();
        });
    }

    _keyReflesh = (sender, public_key_n, public_key_e) => {
        return this.contract.methods
            .keyReflesh(public_key_n, public_key_e)
            .send({from: sender})
            .on('transactionHash', (hash) => {
                console.log(hash);
            }).on('confirmation', (confirmationNumber, receipt) => {
                console.log(confirmationNumber, receipt);
            });
    }

    changePublicKey = () => {
        const state = this.state;
        const user_account = state.userAccount;
        const password = state.passwordForChangePublicKey;
        if (password.length === 0) {
            this.setState({...state, changePublicKeyMessageStatus: "error", changePublicKeyMessageLog: "パスワードを入力してください。"});
            return;
        }
        this._ownership(user_account).then((token_id) => {
            console.log("token id :", token_id);
            this._checkAuthority(user_account).then((res) => {
                const old_n = res.publicKeyN;
                const old_e = res.publicKeyE;
                this.setState({...state, changePublicKeyModal: false, passwordForChangePublicKey: "", messageStatus: "info", messageLog: "パスワードが変更されるのを待機しています。この表示がいつまでも変化しない場合は、 MetaMask を開いてトランザクションを承認しているか確認してください。また、トランザクションが取り込まれるまでに時間がかかっている可能性があります。"});
                const my_rsa_key = keyGen(password);
                const n = my_rsa_key.n.toString();
                const e = String(my_rsa_key.e);
                if (old_n === n && old_e === e) {
                    localStorage.setItem("RSAKey", stringifyRSAKey(my_rsa_key));
                    this.setState({...state, myRSAKey: my_rsa_key, changePublicKeyModal: false, passwordForChangePublicKey: "", messageStatus: "info", messageLog: "秘密鍵を復元しました。"});
                    return;
                }
                this._keyReflesh(user_account, n, e).on("receipt", (result) => {
                    console.log(result);
                    localStorage.setItem("RSAKey", stringifyRSAKey(my_rsa_key));
                    this.setState({...state, myRSAKey: my_rsa_key, changePublicKeyModal: false, passwordForChangePublicKey: "", messageStatus: "success", messageLog: "パスワードを変更しました。"});
                }).on("error", (error) => {
                    console.error(error);
                    this.setState({...state, changePublicKeyModal: false, passwordForChangePublicKey: "", messageStatus: "error", messageLog: "パスワードの変更に失敗しました。"});
                });
            });
        }, (error) => {
            console.error(error);
            this.setState({...state, changePublicKeyModal: false, passwordForChangePublicKey: "", messageStatus: "error", messageLog: "あなたはトークンを持っていません。"});
        });
    }

    componentDidMount() {
        this.setContract();
    }

    render() {
        const state = this.state;
        return (
            <React.Fragment>
                <div className="command-wrapper">
                    <div className="box-title">コマンド</div>
                    <Button color="primary" onClick={this.show("createToken")}>{"トークン作成"}</Button>
                    <Button color="primary" onClick={this.show("deleteToken")}>{"トークン削除"}</Button>
                    <Button color="primary" onClick={this.show("transferToken")}>{"トークン送信"}</Button>
                    <Button color="primary" onClick={this.show("changePublicKey")}>{"パスワード再設定"}</Button>
                    <ButtonDropdown isOpen={state.dropdownOpen} toggle={this.toggle}>
                        <DropdownToggle caret color="primary">{"レポートを取得"}</DropdownToggle>
                        <DropdownMenu onClick={this.selectItem}>
                            {
                                Object.keys(file_name_list).map((file_name) => (
                                    <DropdownItem key={file_name} name={file_name}>
                                        {file_name_list[file_name]}
                                    </DropdownItem>
                                ))
                            }
                        </DropdownMenu>
                    </ButtonDropdown>
                </div>
                <MessageForitemUrls urls={state.itemUrls} />
                <MessageForNetwork currentNetwork={state.currentNetwork} />
                <MessageForAccount error={state.noAccount} userAccount={state.userAccount} />
                <MessageForToken error={state.noToken} isHolder={state.isHolder} />
                <Message message={state.messageLog} status={state.messageStatus} />

                <Modal isOpen={this.state.createTokenModal} toggle={this.hide("createToken")}>
                    <ModalHeader toggle={this.hide("createToken")}>{"トークン作成"}</ModalHeader>
                    <ModalBody>
                        <div className="input-wrapper"><input type={"password"} name={"passwordForCreateToken"} className="form-control" placeholder="パスワードを入力してください" onChange={this.handleChange} value={state["passwordForCreateToken"]} /></div>
                        <Message message={state.createTokenMessageLog} status={state.createTokenMessageStatus} />
                    </ModalBody>
                    <ModalFooter>
                        <Button color="primary" onClick={this.createToken}>{"トークン作成"}</Button>
                        <Button color="secondary" onClick={this.hide("createToken")}>{"キャンセル"}</Button>
                    </ModalFooter>
                </Modal>

                <Modal isOpen={this.state.deleteTokenModal} toggle={this.hide("deleteToken")}>
                    <ModalHeader toggle={this.hide("deleteToken")}>{"トークン削除"}</ModalHeader>
                    <ModalBody>
                        <p>本当にトークンを削除しますか？</p>
                    </ModalBody>
                    <ModalFooter>
                        <Button color="primary" onClick={this.deleteToken}>{"トークン削除"}</Button>
                        <Button color="secondary" onClick={this.hide("deleteToken")}>{"キャンセル"}</Button>
                    </ModalFooter>
                </Modal>

                <Modal isOpen={this.state.transferTokenModal} toggle={this.hide("transferToken")}>
                    <ModalHeader toggle={this.hide("transferToken")}>{"トークン送信"}</ModalHeader>
                    <ModalBody>
                        <div className="input-wrapper"><input type={"text"} name={"transferTokenTo"} className="form-control" placeholder="送信先のアドレスを入力してください" onChange={this.handleChange} value={state["transferTokenTo"]} /></div>
                        <Message message={state.transferTokenMessageLog} status={state.transferTokenMessageStatus} />
                    </ModalBody>
                    <ModalFooter>
                        <Button color="primary" onClick={this.transfer}>{"トークン送信"}</Button>
                        <Button color="secondary" onClick={this.hide("transferToken")}>{"キャンセル"}</Button>
                    </ModalFooter>
                </Modal>

                <Modal isOpen={this.state.changePublicKeyModal} toggle={this.hide("changePublicKey")}>
                    <ModalHeader toggle={this.hide("changePublicKey")}>{"パスワード再設定"}</ModalHeader>
                    <ModalBody>
                        <div className="input-wrapper"><input type={"password"} name={"passwordForChangePublicKey"} className="form-control" placeholder="パスワードを入力してください" onChange={this.handleChange} value={state["passwordForChangePublicKey"]} /></div>
                        <Message message={state.changePublicKeyMessageLog} status={state.changePublicKeyMessageStatus} />
                    </ModalBody>
                    <ModalFooter>
                        <Button color="primary" onClick={this.changePublicKey}>{"パスワード再設定"}</Button>
                        <Button color="secondary" onClick={this.hide("changePublicKey")}>{"キャンセル"}</Button>
                    </ModalFooter>
                </Modal>
            </React.Fragment>
        );
    }
}

function parseRSAKey(keyJSON) {
    if (keyJSON) {
        return {
            coeff: bigInt(keyJSON.coeff),
            d: bigInt(keyJSON.d),
            dmp1: bigInt(keyJSON.dmp1),
            dmq1: bigInt(keyJSON.dmq1),
            e: keyJSON.e,
            n: bigInt(keyJSON.n),
            p: bigInt(keyJSON.p),
            q: bigInt(keyJSON.q)
        };
    }
}

function stringifyRSAKey(RSAKey) {
    if (RSAKey) {
        return JSON.stringify({
            coeff: RSAKey.coeff.toString(),
            d: RSAKey.d.toString(),
            dmp1: RSAKey.dmp1.toString(),
            dmq1: RSAKey.dmq1.toString(),
            e: RSAKey.e,
            n: RSAKey.n.toString(),
            p: RSAKey.p.toString(),
            q: RSAKey.q.toString()
        });
    }
}

function keyGen(pass) {
    return cryptico.generateRSAKey(pass, 1024);
}

function signRSA(my_rsa_key) {
    const intd = "1".concat("0".repeat(200));
    const d = bigInt(my_rsa_key.d.toString());
    const n = bigInt(my_rsa_key.n.toString());
    const m = bigInt(intd);
    return m.modPow(d, n).toString();
}

function MessageForitemUrls(props) {
    const urls = props.urls;
    return (Object.keys(urls).length === 0) ? "" : (
        <div className="command-wrapper">
        <div className="box-title">アイテム</div>
        {
            Object.keys(urls).map((domain) => {
                return (<div key={domain}><a href={urls[domain]} target="_blank" rel="noopener noreferrer">{file_name_list[domain]}</a></div>);
            })
        }
        </div>
    );
}

function MessageForNetwork(props) {
    const current_network = props.currentNetwork;
    return (current_network == null) ? (
        <div className="error-message">
            <div>{"ネットワークに接続されていません。"}</div>
                <div>{"しばらくしてもこの表示が消えない場合、MetaMask が起動していることを確認してください。"}</div>
                <div>{"MetaMask の状態が正常であるにもかかわらずこの表示が出ている場合は、ブラウザをリロードして問題が解決するか試してください。"}</div>
        </div>
    ) : (current_network === "1") ? (
        <div className="info-message">
            <div>{"接続中のネットワーク"}</div>
            <div className="padding-left">{"Mainnet"}</div>
        </div>
    ) : (props.currentNetwork === "3") ? (
        <div className="warning-message">
            <div>{"このネットワークではレポート閲覧以外のことができます。"}</div>
            <div>{"接続中のネットワーク"} : </div>
            <div className="padding-left">{"Ropsten"}</div>
        </div>
    ) : (props.currentNetwork === "4") ? (
        <div className="warning-message">
            <div>{"このネットワークではレポート閲覧以外のことができます。"}</div>
            <div>{"接続中のネットワーク"} : </div>
            <div className="padding-left">{"Rinkeby"}</div>
        </div>
    ) : (
        <div className="error-message">
            <div>{"このサイトではサポートされていないネットワークに接続されています。"}</div>
            <div>{"接続中のネットワークID"}</div>
            <div className="padding-left">{current_network}</div>
        </div>
    );
}

function MessageForAccount(props) {
    const address = props.userAccount;
    return (props.error) ? (
        <div className="error-message">{"アカウントが取得できません。"}</div>
    ) : (
        <div className="info-message">
            <div>{"選択中のアカウントアドレス"}</div>
            <div className="padding-left" title={address}>{address.slice(0, 6)}{"..."}{address.slice(-4)}</div>
        </div>
    );
}

function MessageForToken(props) {
    return (props.error) ? (
        <div className="error-message">
            <div>{"トークンを持っていないため、レポートを見ることができません。"}</div>
            <div>{"トークン作成ボタンから手に入れることができます。"}</div>
        </div>
    ) : (props.isHolder) ? (
        <div className="success-message">{"レポートを見ることができます"}</div>
    ) : (
        <div className="warning-message">{"レポートを見ることができません。"}</div>
    );
}

function Message(props) {
    const messageStatus = props.status;
    return (messageStatus === "hidden") ? "" : (
        <div className={`${messageStatus}-message`}>{props.message}</div>
    );
}
